<template>
  <div>
    <Row>
      <Col span="4" style="min-width: 200px;">
      <Card>
        <p>
          <Icon type="ios-list-outline"></Icon>
          
        </p>
        <Tree :data="treeMenuList"
              @on-toggle-expand="handleToggleExpand"
              @on-select-change="handleSelectChange">
        </Tree>
      </Card>
      </Col>
      <Col span="20" class="padding-left-10">
      <div class="table-btn">
        <Button type="primary" @click="handleShowAddModel(1)"></Button>
        <Button type="primary" @click="handleShowAddModel(2)" :disabled="isAddMenuBtnDisabled||sels.length == 0"></Button>
        <Button type="error" @click="handleDeleteMenu" :disabled="sels.length==0"></Button>
      </div>
      <Table ref="selection" :columns="tableColumns" :data="tableData"
             @on-selection-change="handleSelectionChange">
      </Table>
      </Col>
    </Row>
    <!---->
    <Modal
      v-model="showAddModal"
      :title="addModalTitle"
      @on-visible-change="handleVisibleChange">
      <p slot="footer">
        <Button type="ghost" @click="showAddModal = false"></Button>
        <Button type="primary" @click="handleAddMenu"></Button>
      </p>
      <Form :model="addMenuForm" ref="addMenuForm" :rules="addMenuFormValidate" :label-width="70">
        <FormItem label="" prop="parent" v-if="addParentVisible">
          {{parentName}}
        </FormItem>
        <FormItem label="" prop="title">
          <Input v-model.trim="addMenuForm.title" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input v-model.trim="addMenuForm.name" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="path">
          <Input v-model.trim="addMenuForm.path" placeholder="url"></Input>
        </FormItem>
        <FormItem label="" prop="icon" v-if="!addParentVisible">
          <Input v-model.trim="addMenuForm.icon"></Input>
        </FormItem>
        <FormItem label="" prop="identifier">
          <Input v-model.trim="addMenuForm.identifier" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="orders">
          <InputNumber :max="10" :min="1" v-model="addMenuForm.orders"></InputNumber>
        </FormItem>
      </Form>
    </Modal>
    <!---->
    <Modal
      v-model="showEditModal"
      :title="editModalTitle">
      <p slot="footer">
        <Button type="ghost" @click="showEditModal = false"></Button>
        <Button type="primary" @click="handleEditMenu"></Button>
      </p>
      <Form :model="editMenuForm" ref="editMenuForm" :rules="editMenuFormValidate" :label-width="70">
        <FormItem label="" prop="parent" v-if="editMenuForm.parent">
          {{editParentName}}
        </FormItem>
        <FormItem label="" prop="title">
          <Input v-model.trim="editMenuForm.title" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input v-model.trim="editMenuForm.name" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="path">
          <Input v-model.trim="editMenuForm.path" placeholder="url"></Input>
        </FormItem>
        <FormItem label="" prop="icon" v-if="!editMenuForm.parent">
          <Input v-model.trim="editMenuForm.icon"></Input>
        </FormItem>
        <FormItem label="" prop="identifier">
          <Input v-model.trim="editMenuForm.identifier" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="orders">
          <InputNumber :max="10" :min="1" v-model="editMenuForm.orders"></InputNumber>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
  export default {
    data() {
      const validateAddIcon = (rule, value, callback) => {
        if(this.addParentVisible){
          return callback();
        }
        if (!value) {
          return callback(new Error(''));
        }
        callback();
      };
      const validateEditIcon = (rule, value, callback, flag) => {
        if(this.editMenuForm.parent){
          return callback();
        }
        if (!value) {
          return callback(new Error(''));
        }
        callback();
      };
      const validateAddIdentifier = (rule, value, callback) => {
        if(this.addParentVisible){
          return callback();
        }
        if (!value) {
          return callback(new Error(''));
        }
        callback();
      };
      const validateEditIdentifier = (rule, value, callback) => {
        if(this.editMenuForm.parent){
          return callback();
        }
        if (!value) {
          return callback(new Error(''));
        }
        callback();
      };
      return {
        treeMenuList: [],
        tableColumns: [
          {type: 'selection', width: 60, align: 'center'},
          {
            title: '', key: 'name', width: 100, align: 'center',
            render: (h, params) => {
              return h('div', {
                domProps: {
                  innerHTML: !params.row.parent ? '' : params.row.name.indexOf('-') > -1 ? params.row.name.split('-')[0] : params.row.title
                }
              })
            }
          },
          {title: '', key: 'title', width: 100, align: 'center'},
          {
            title: '', key: 'name', width: 100, align: 'center',
            render: (h, params) => {
              return h('div', {
                domProps: {
                  innerHTML: params.row.name.indexOf('-') > -1 ? params.row.name.split('-')[1] : params.row.name
                }
              })
            }
          },
          {title: '', key: 'path', width: 100, align: 'center'},
          {title: '', key: 'icon', width: 80, align: 'center'},
          {title: '', key: 'identifier', width: 150, align: 'center'},
          {title: '', key: 'orders', minWidth: 80, align: 'center'},
         /* {title: '', key: 'status', minWidth: 60, align: 'center'},*/
          {
            title: '',
            key: '',
            width: 160,
            align: 'center',
            fixed: 'right',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'ghost',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.handleShowEditModel(params.row)
                    }
                  }
                }, ''),
               /* h('Button', {
                  props: {
                    type: 'ghost',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      params.row.status == 1 ? this.setMenuStatus(params.row, 2) : this.setMenuStatus(params.row, 1)
                    }
                  }
                }, params.row.status == 1 ? '' : '')*/
              ]);
            }
          }
        ],
        tableData: [],
        sels: [],//
        showAddModal: false,
        addModalTitle: '',
        searchForm: {
          currentParentId: '',
        },
        parentName: '',
        addMenuForm: {
          parent: '',
          title: '',
          name: '',
          path: '',
          icon: '',
          identifier: '',
          type: '',
          orders: 1,
          status: '1',
        },
        addMenuFormValidate: {
          title:{required:true,message:'',trigger: 'blur'},
          name:{required:true,message:'',trigger: 'blur'},
          path:{required:true,message:'',trigger: 'blur'},
          icon:{required:true,validator:validateAddIcon,trigger: 'blur'},
          identifier:{required:true,validator:validateAddIdentifier,trigger: 'blur'},
        },
        addParentVisible: false,
        addIdentifierVisible: false,
        isAddMenuBtnDisabled: true,

        showEditModal: false,
        editModalTitle: '',
        editParentName: '',
        editParentVisible: false,
        editMenuForm: {
          parent: '',
          title: '',
          name: '',
          path: '',
          icon: '',
          identifier: '',
          orders: null,
          status: '',
          type: '',
          menuId: ''
        },
        editMenuFormValidate: {
          title:{required:true,message:'',trigger: 'blur'},
          name:{required:true,message:'',trigger: 'blur'},
          path:{required:true,message:'',trigger: 'blur'},
          icon:{required:true,validator:validateEditIcon,trigger: 'blur'},
          identifier:{required:true,validator:validateEditIdentifier,trigger: 'blur'},
        },
      }
    },
    methods: {
      initFields() {
        this.addMenuForm = {
          parent: '',
          title: '',
          name: '',
          path: '',
          icon: '',
          type: '',
          orders: 1,
          status: '1',
        }
      },
      //-
      handleVisibleChange(show){
        if(!show){
          this.initFields();
        }
      },
      getMenuList() {
        let param = {
          currentParentId: this.searchForm.currentParentId
        }
        this.$api.getMenuList(param).then((res) => {
          if (this.$util.checkResCode(this, res)) {
            this.tableData = res.data;
            this.parentName = res.data.title;
          }
        }).catch((err) => {
          this.$util.showCatchErr(this, err);
        })
      },
      getTreeMenuList() {
        let param = {
          currentParentId: this.searchForm.currentParentId
        }
        this.$api.getTreeMenuList(param).then((res) => {
          if (this.$util.checkResCode(this, res)) {
            let menuArr = res.data, objectData = [];
            for (let i = 0; i < menuArr.length; i++) {
              if (menuArr[i].haveChild == "true") {
                objectData.push({
                  parent: menuArr[i].parent,
                  id: menuArr[i].menuId,
                  title: menuArr[i].title,
                  children: [{
                    title: '',
                  }]
                });
              } else {
                objectData.push({
                  parent: menuArr[i].parent,
                  id: menuArr[i].menuId,
                  title: menuArr[i].title,
                  children: [],
                });
              }
            }
            var menuData = this.searchForm.currentParentId;
            if (menuData == '') {
              //
              this.treeMenuList = objectData;
            } else {
              this.setChildrenList(this.treeMenuList, this.searchForm.currentParentId, objectData);
            }
            //
            this.getMenuList();

          }
        }).catch((err) => {
          this.$util.showCatchErr(this, err);
        })
      },
      //
      setChildrenList(menuData, currentParentId, objectData) {
        if (menuData.length <= 0) {
          return;
        }
        for (var i = 0; i < menuData.length; i++) {
          if (menuData[i].id == currentParentId) {
            menuData[i].children = objectData;
          }
          if (menuData[i].children) {
            this.setChildrenList(menuData[i].children, currentParentId, objectData);
          }
        }
      },
      //--
      handleToggleExpand(node) {
        if (node.expand) {
          this.searchForm.currentParentId = node.id;
          //
          this.getTreeMenuList();
        }
      },
      //--
      handleSelectChange(nodeArr) {
        this.searchForm.currentParentId = nodeArr[0].id;
        //
        this.getMenuList();
      },
      //
      handleShowAddModel(identify) {
        this.showAddModal = true;
        this.$refs['addMenuForm'].resetFields();
        if (identify == 1) {
          this.addParentVisible = false;
          this.addModalTitle = '';
          return;
        }
        this.addParentVisible = true;
        this.addModalTitle = '';
      },
      handleAddMenu() {
        this.$refs.addMenuForm.validate((valid) => {
          if (!valid) {
            return false;
          }
          let param = Object.assign({}, this.addMenuForm);
          this.$api.addMenu(param).then((res) => {
            if (this.$util.checkResCode(this, res)) {
              this.$Message.success('');
              this.showAddModal = false;
              this.initFields();
              this.getMenuList();
            }
          }).catch((err) => {
            this.$util.showCatchErr(this, err)
          })
        })
      },
      handleSelectionChange(sel) {
        this.sels = sel;
        if (this.sels.length >= 1 && this.sels[0].parent == 0) {
          this.isAddMenuBtnDisabled = false;
          this.addMenuForm.parent = this.sels[0].menuId;
          this.parentName = this.sels[0].title;
          this.addMenuForm.type = this.sels[0].name;
        } else {
          this.isAddMenuBtnDisabled = true;
        }
      },
      handleEditMenu() {
        this.$refs.editMenuForm.validate((valid) => {
          if (!valid) {
            return false;
          }
          let param = Object.assign({}, this.editMenuForm)
          this.$api.editMenu(param).then((res) => {
            if (this.$util.checkResCode(this, res)) {
              this.$Message.success('');
              this.showEditModal = false;
              this.initFields();
              this.getMenuList();
            }
          }).catch((err) => {
            this.$util.showCatchErr(this, err)
          })
        })

      },
      handleShowEditModel(row) {
        this.showEditModal = true;
        console.log(row)
        this.editModalTitle = row.parent ? '' : '';
        this.editParentName = !row.parent ? '' : row.name.indexOf('-') > -1 ? row.name.split('-')[0] : row.title;
        this.editMenuForm = {
          parent: row.parent,
          title: row.title,
          name: row.name,
          path: row.path,
          icon: row.icon,
          type: row.type,
          identifier: row.identifier,
          orders: row.orders,
          status: row.status,
          menuId: row.menuId
        };
      },
      setMenuStatus(row, status) {

      },
      handleDeleteMenu() {
        var menuIds = this.sels.map(item => item.menuId).toString();
        let param = {menuIds: menuIds};
        this.$Modal.confirm({
          title: '',
          content: '<p></p>',
          onOk: () => {
            this.$api.deleteMenu(param).then((res) => {
              if (this.$util.checkResCode(this, res)) {
                this.$Message.success('');
                this.getMenuList();
                this.getTreeMenuList();
                this.sels = [];
              }
            }).catch((err) => {
              this.$util.showCatchErr(this, err);
            });
          },
          onCancel: () => {

          }
        });
      }
    },
    mounted() {
      this.getMenuList();
      this.getTreeMenuList();
    }
  }
</script>

<style lang="less" scoped>
  @import '../../styles/menu-list.less';

</style>



