import lazyLoading from './lazyLoading'
import Vue from 'vue';

export default (routers, data) => {
  //componentkey
  //
  generaMenu(routers, data)
}

function generaMenu(routers, data) {
  if (data) {
    data.forEach((item) => {
      let menu = Object.assign({}, item);
      if(menu.component){
        if (menu.component == "Main") {
          menu.component = require("@/views/Main.vue");
          //console.log(menu.component)
        }else {
          // template or render function not defined.vue-loader importrequire
          menu.component = lazyLoading(menu.component);
          //menu.component = require("@/views/"+menu.component+".vue")
          // requireoptionsVue.extendnew   
          /* const temp = require("@/views/"+menu.component+".vue").default;
           const Constructor = Vue.extend(temp)
           menu.component = new Constructor()
           */
        }
      }else{
        //component  null ''
        menu.component = null;
      }
      if (item.children) {//
        menu.children = [];
        generaMenu(menu.children, item.children)
      }
      routers.push(menu);
    })
  }
}



