<template>
  <div class="login-wrap">
    <div class="login-container" @keyup.enter="handleLogin('loginForm')">
      <h1 class="title">{{title = loginFormVisible ? 'Welcome to Aimee\'s blog~' : 'Regist'}}</h1>
      <Form ref="loginForm" :model="loginForm" :rules="loginFormRules" v-if="loginFormVisible">
        <FormItem prop="user">
          <Input type="text" v-model="loginForm.username" placeholder="">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="password">
          <Input type="password" v-model="loginForm.password" placeholder="">
          <Icon type="ios-locked-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem>
          <Button type="primary" class="login-btn" @click="handleLogin('loginForm')"> </Button>
        <!--  <a class="regist" @click="loginFormVisible = false"></a>-->
        </FormItem>
      </Form>
     <!-- <Form ref="registerForm" :model="registerForm" :rules="registerFormRules" v-else>
        <FormItem prop="user">
          <Input type="text" v-model="registerForm.username" placeholder="">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="password">
          <Input type="password" v-model="registerForm.password" placeholder="">
          <Icon type="ios-locked-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem>
          <Button type="primary" class="regist-btn" @click="handleRegist('registerForm')"> </Button>
          <Button type="primary" class="regist-cancel-btn" @click="loginFormVisible = true"> </Button>
        </FormItem>
      </Form>-->
    </div>
  </div>
 </template>

 <script>
   import MenuUtils from '../scripts/common/menu/menuUtils'
   export default {
     data () {
       return {
         title:'',
         loginForm:{
           username:'',
           password:''
         },
         loginFormRules:{
           username:{required:true,message:'',trigger: 'blur'},
           password:{required:true,message:'',trigger: 'blur'},
         },
        /* registerForm:{
           username:'',
           password:''
         },
         registerFormRules:{
           username:{required:true,message:'',trigger: 'blur'},
           password:{required:true,message:'',trigger: 'blur'},
         },*/
         loginFormVisible:true,
       }
     },
     components:{},
     methods: {
       handleLogin(formName){
         this.$refs[formName].validate((valid)=>{
           if(!valid){
             return;
           }
           let params = {
               user_name:this.loginForm.username,
               user_password:this.loginForm.password
           }
           this.$api.login(params).then((res)=>{
             if(this.$util.checkResCode(this,res)){
              // window.location.href = '/home';
               sessionStorage.setItem('user', this.loginForm.username);
               sessionStorage.setItem('token', res.token);
               //
               this.$api.getCategoryMenu().then((res)=>{
                 if(this.$util.checkResCode(this,res)){
                   let menuData = res.data;
                   if (!menuData) {
                     return;
                   }
                   this.$router.push({
                     name: 'home_index'
                   });
                 }
               }).catch((err)=>{
                 this.$util.showCatchErr(this,err);
               })

             }else{
               this.$Modal.warning({
                 title: '',
                 content: ''+ res.msg,
                 okText: ''
               })
             }
           }).catch(err=>{
             this.$util.showCatchErr(this, err,"");
           })
         })

       },
      /* handleRegist(formName){
         this.$refs[formName].validate((valid)=>{
           if(!valid){
             return;
           }
           let param = {
             user_name:this.registerForm.username,
             user_password:this.registerForm.password
           }
           this.$api.register(param).then((res)=>{
             if(this.$util.checkResCode(this,res)){
               this.$Modal.confirm({
                 title: '',
                 content: '',
                 okText: '',
                 cancelText: '',
                 onOk:(()=>{
                   window.location.href = '/home';
                 }),
                 onCancel:(()=>{
                   this.loginFormVisible = true;
                 })
               })
             }
           }).catch(err=>{
             this.$util.showCatchErr(this, err,"");
           })
         })
       }*/
     }
    }
</script>

<style  scoped  lang="less">
  @import '../styles/login.less';
</style>



