<template>
  <div>
    <Row>
      <Col span="9">
      <Card>
        <p>
          <Icon type="android-contacts"></Icon>
          
          <Button v-if="isUserLogin" class="right-float" type="ghost" shape="circle" @click="showEditUserInfoModal = true">
          </Button>
        </p>
        <div class="user-img">
          <img src="../../images/user-img.jpg"/>
        </div>
        <div class="margin-top-10">
          <h4 class="text-weight">Aimee</h4>
          <p>
            <Icon type="location"></Icon>
            {{userInfo.address}}
          </p>
          <h5 class="text-weight"></h5>
          <div>
            <p>{{userInfo.skill}}</p>
            <p>{{userInfo.introduction}}</p>
          </div>
        </div>
      </Card>
      </Col>
      <Col span="15" class="homepage-wrap padding-left-10">
      <Card>
        <p>
          <Icon type="ios-list"></Icon>
          
          <span v-if="isUserLogin">
            <Button class="right-float margin-left-10" type="ghost" shape="circle"
                       @click="showProjectTableModal = true"></Button>
            <Button class="right-float" type="ghost" shape="circle" @click="showAddProjectModal = true"></Button>
          </span>
        </p>
        <Timeline class="timeLine-box" style="clear:both;" >
          <template v-for="item in projectList">
            <TimelineItem>
              <p class="time">{{item.time}}</p>
              <p class="content">{{item.introduction}}</p>
            </TimelineItem>
          </template>
        </Timeline>
      </Card>
      </Col>
    </Row>

    <!---->
    <Modal
      v-model="showEditUserInfoModal"
      title="">
      <p slot="footer">
        <Button type="ghost" @click="showEditUserInfoModal = false"></Button>
        <Button type="primary" @click="handleEditUserInfo"></Button>
      </p>
      <Form :model="editUserForm" ref="editUserForm" :rules="editUserFormValidate" :label-width="70">
        <FormItem label="" prop="address">
          <Input v-model.trim="editUserForm.address"></Input>
        </FormItem>
        <FormItem label="" prop="skill">
          <Input v-model.trim="editUserForm.skill"></Input>
        </FormItem>
        <FormItem label="" prop="introduction">
          <Input v-model.trim="editUserForm.introduction"></Input>
        </FormItem>
      </Form>
    </Modal>

    <!---->
    <Modal
      v-model="showAddProjectModal"
      title="">
      <p slot="footer">
        <Button type="ghost" @click="showAddProjectModal = false"></Button>
        <Button type="primary" @click="handleAddProject"></Button>
      </p>
      <Form :model="addProjectForm" ref="addProjectForm" :rules="addProjectFormValidate"  :label-width="90">
        <FormItem label="" prop="time">
          <DatePicker type="date" placeholder="" v-model.trim="addProjectForm.time"
                      style="width: 200px" :editable="false">
          </DatePicker>
        </FormItem>
        <FormItem label="" prop="introduction">
          <Input v-model.trim="addProjectForm.introduction"></Input>
        </FormItem>
        <FormItem label="" prop="url">
          <Input v-model.trim="addProjectForm.url"></Input>
        </FormItem>
      </Form>
    </Modal>

    <!---->
    <Modal
      v-model="showProjectTableModal"
      title="" width="800">
      <p slot="footer"></p>
      <Table ref="selection" :columns="tableColumns" :data="projectList">
      </Table>
    </Modal>

    <!---->
    <Modal
      v-model="showEditProjectModal"
      title="">
      <p slot="footer">
        <Button type="ghost" @click="showEditProjectModal = false"></Button>
        <Button type="primary" @click="handleEditProject"></Button>
      </p>
      <Form :model="editProjectForm" ref="editProjectForm" :rules="editProjectFormValidate" :label-width="90">
        <FormItem label="" prop="time">
          <DatePicker type="date"  v-model.trim="editProjectForm.time"
                      style="width: 200px" :editable="false">
          </DatePicker>
        </FormItem>
        <FormItem label="" prop="introduction">
          <Input v-model.trim="editProjectForm.introduction"></Input>
        </FormItem>
        <FormItem label="" prop="url">
          <Input v-model.trim="editProjectForm.url"></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        isUserLogin: false,
        projectList: [],
        userInfo: {
          address: '',
          skill: '',
          introduction: ''
        },
        //
        showEditUserInfoModal:false,
        editUserForm:{
          id:'',
          address:'',
          skill:'',
          introduction:'',
        },
        editUserFormValidate:{
          address:{required:true,message:'',trigger: 'blur'},
          skill:{required:true,message:'',trigger: 'blur'},
          introduction:{required:true,message:'',trigger: 'blur'},
        },

        //
        showAddProjectModal:false,
        addProjectForm:{
          time:'',
          introduction:'',
          url:'',
        },
        addProjectFormValidate:{
          time:{required:true,message:'',trigger: 'blur'},
          introduction:{required:true,message:'',trigger: 'blur'},
        },

        //
        showProjectTableModal: false,
        tableColumns: [
          {title: '', key: 'time', width: 130, align: 'center',},
          {title: '', key: 'introduction', minWidth: 160, align: 'center'},
          {title: '', key: 'url', width: 160, align: 'center',},
          {
            title: '',
            key: '',
            width: 130,
            align: 'center',
            fixed: 'right',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'ghost',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.handleShowProjectEditModel(params.row)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'ghost',
                    size: 'small'
                  },
                  on: {
                    click: () => {

                    }
                  }
                }, '')
              ]);
            }
          }
        ],
        //
        showEditProjectModal:false,
        editProjectForm:{
          id:'',
          time:'',
          introduction:'',
          url:'',
        },
        editProjectFormValidate:{
          time:{required:true,message:'',trigger: 'blur'},
          introduction:{required:true,message:'',trigger: 'blur'},
        }
      }
    },
    methods: {
      getUserInfo() {
        this.$api.getUserInfo().then((res) => {
          if (this.$util.checkResCode(this, res)) {
            let userInfo = res.data[0];
            this.userInfo = {
              address: userInfo.address,
              skill: userInfo.skill,
              introduction: userInfo.introduction
            }
            this.editUserForm = {
              id: userInfo.id,
              address: userInfo.address,
              skill: userInfo.skill,
              introduction: userInfo.introduction
            }
          }
        }).catch((err) => {
          this.$util.showCatchErr(this, err);
        })
      },
      getProjectInfo(){
        this.$api.getProjectInfo().then((res) => {
          if (this.$util.checkResCode(this, res)) {
            this.projectList = res.data;
          }
        }).catch((err) => {
          this.$util.showCatchErr(this, err);
        })

      },
      handleEditUserInfo() {
        this.$refs.editUserForm.validate((valid) => {
          if (!valid) {
            return false;
          }
          let param = Object.assign(this.editUserForm);
          this.$api.editUserInfo(param).then((res) => {
            if (this.$util.checkResCode(this, res)) {
              this.$Message.success('');
              this.showEditUserInfoModal = false;
              this.getUserInfo();
            }
          }).catch((err) => {
            this.$util.showCatchErr(this, err);
          })
        })
      },
      handleAddProject() {
        this.$refs.addProjectForm.validate((valid) => {
          if (!valid) {
            return false;
          }
          let param = Object.assign(this.addProjectForm);
          param.time = this.$util.formatToDate(param.time);
          this.$api.addProject(param).then((res) => {
            if (this.$util.checkResCode(this, res)) {
              this.$Message.success('');
              this.showAddProjectModal = false
              this.getProjectInfo();
            }
          }).catch((err) => {
            this.$util.showCatchErr(this, err);
          })
        })
      },
      handleShowProjectEditModel(row){
        this.showEditProjectModal = true;
        this.editProjectForm = {
          id: row.id,
          time: row.time,
          introduction: row.introduction,
          url: row.url,
        };
      },
      handleEditProject() {
        this.$refs.editProjectForm.validate((valid) => {
          if (!valid) {
            return false;
          }
          let param = Object.assign(this.editProjectForm);
          param.time = this.$util.formatToDate(param.time);
          this.$api.editProject(param).then((res) => {
            if (this.$util.checkResCode(this, res)) {
              this.$Message.success('');
              this.showEditProjectModal = false;
              this.getProjectInfo();
            }
          }).catch((err) => {
            this.$util.showCatchErr(this, err);
          })
        })
      },
    },
    mounted() {
      this.isUserLogin = window.sessionStorage.getItem('user') ? true : false;
      this.getUserInfo();
      this.getProjectInfo();
    }
  }
</script>
<style scoped lang="less">
  @import "../../styles/homepage.less";
  @import "../../styles/common/common.less";
</style>



